import React, { useState, useEffect } from "react";
import { Route, Routes, useLocation, useParams } from "react-router-dom";

// extend Components
import ProgressLoad from "../components/ProgressLoad.js";
import ErrorMessage from "../components/ErrorMessage.js";

// Ship Components
import ShipRoute from "../views/Ship/ShipRoute";
import ShipList from "../views/Ship/ShipList";

const CACHE = {};

async function fetchLink(index, link)
{
  if (CACHE[index])
    return CACHE[index];

  const DATA = await fetch(link).then(res => res.json());
  CACHE[index] = DATA;

  return DATA;
};

function Ship()
{
  const { pathname }            = useLocation();
  const [error, setError]       = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [azurapi, setAzurapi]   = useState([]);

  const [bannerIsLoaded, setBannerIsLoaded] = useState(false);
  const [shipBanner, setShipBanner]         = useState([]);

  useEffect(() => window.scrollTo(0, 0), [pathname]);

  useEffect(() =>
  {
    fetchLink("azurapi", "https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/ships.json")
      .then(
        (result) => { setIsLoaded(true); setAzurapi(result) },
        (err) => { setIsLoaded(true); setError(err) }
      );

    fetchLink("banner", "https://raw.githubusercontent.com/Fernando2603/AzurLane/main/ShipBanner.json")
      .then(
        (result) => { setBannerIsLoaded(true); setShipBanner(result) },
        (err) => { setBannerIsLoaded(true); setError(err) }
      );
  }, []);

  // in parent function because useParams can't be used in class
  const ShipRouteMatch = () =>
  {
    const { id }    = useParams();
    let id_azurapi  = id.toLowerCase();
    const ID_BANNER = id_azurapi;

    if (id_azurapi === "hiryuu_meta") id_azurapi = "hiryu.meta";
    if (id_azurapi === "ark_royal_meta") id_azurapi = "royal.meta";

    const SHIP_DATA = azurapi.find(ship => ship.names.en.toLowerCase().replace(/\s+/g, "_") === id_azurapi);
    const BANNER_DATA = shipBanner.find(banner => banner.name.toLowerCase().replace(/\s+/g, "_") === ID_BANNER);
    return <ShipRoute shipData={SHIP_DATA} bannerData={BANNER_DATA} paramsId={id} />;
  };

  if (error)
    return <ErrorMessage text={error.message} />;
  else if (!isLoaded)
    return <ProgressLoad text="Loading AzurAPI..." />;
  else if (!bannerIsLoaded)
    return <ProgressLoad text="Loading ShipBanner..." />;

  return (
    <>
      <Routes>
        <Route path=":id" element={<ShipRouteMatch />} />
        <Route path="" element={<ShipList azurapi={azurapi} shipBanner={shipBanner}/>} />
      </Routes>
    </>
  );
}

export default Ship;
