import React, { Component } from "react";
import PropTypes from "prop-types";

import EquipmentFetch   from "./EquipmentFetch.js";
import EquipmentProfile from "./EquipmentProfile.js";

const propTypes = {
  equipmentData: PropTypes.object,
  paramsId: PropTypes.string
};

export default class EquipmentRoute extends Component
{
  render()
  {
    const { equipmentData, paramsId } = this.props;

  	if (equipmentData === undefined)
  	  return <EquipmentFetch paramsId={paramsId} />;

    return <EquipmentProfile equipmentData={equipmentData} />;
  };
};

EquipmentRoute.propTypes = propTypes;
