import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import { Input } from "reactstrap";

// core components
import Navbar       from "../components/Navbar/Navbar.js";
import PageHeader   from "../components/PageHeader/PageHeader.js";
import Footer       from "../components/Footer/Footer.js";
import ProgressLoad from "../components/ProgressLoad.js";
import ErrorMessage from "../components/ErrorMessage.js";

import Profile from "./Calculator/Profile.js";
import Stats   from "./Calculator/Stats.js";

const CACHE = {};

async function fetchLink(index, link)
{
  if (CACHE[index])
    return CACHE[index];

  const DATA = await fetch(link).then(res => res.json());
  CACHE[index] = DATA;

  return DATA;
};

function Calculator()
{
  const { pathname }              = useLocation();
  const [error, setError]         = useState(null);
  const [isLoaded, setIsLoaded]   = useState(false);
  const [stats, setStats]         = useState([]);
  const [ship, setShip]           = useState({});
  const [statsData, setStatsData] = useState({
    health: 0,
    firepower: 0,
    aviation: 0,
    torpedo: 0,
    evasion: 0,
    antiair: 0,
    reload: 0,
    luck: 0,
    speed: 0,
    hit: 0,
    asw: 0
  });

  function statsProcessor(data)
  {
    let database = [];
    data.forEach((idx) =>
    {
      if (idx.id === "30001") idx.name = "Hiryuu META";
      if (idx.id === "30002") idx.name = "Ark Royal META";
      database.push(idx);
    });
    database.sort((a, b) =>
    {
      const NAME_A = a.name.toLowerCase();
      const NAME_B = b.name.toLowerCase();

      if (NAME_A < NAME_B) return -1;
      if (NAME_A > NAME_B) return 1;

      return 0;
    });
    setStats(database);
  };

  useEffect(() => window.scrollTo(0, 0), [pathname]);

  useEffect(() =>
  {
    fetchLink("statistics", "https://raw.githubusercontent.com/Asterisk-api/AzurLane/main/statistics.json")
      .then(
        (result) => { setIsLoaded(true); statsProcessor(result) },
        (errors) => { setIsLoaded(true); setError(errors) }
      );
  }, []);

  if (error)
    return <ErrorMessage text={error.message} />;
  else if (!isLoaded)
    return <ProgressLoad text="Loading ..." />;

  const shipChange = (event) =>
  {
    const VALUE = event.target.value;
    if (VALUE === "000") setShip({});
    else setShip(stats.find(idx => idx.id === VALUE));
  };

  const profileData = (idx) =>
  {
    if (!idx.ship.id)
      return setStatsData({
        health: 0,
        firepower: 0,
        torpedo: 0,
        antiair: 0,
        aviation: 0,
        reload: 0,
        hit: 0,
        evasion: 0,
        luck: 0,
        asw: 0
      });

    const SHIP  = idx.ship;
    const LEVEL = idx.level;
    const BONUS = (idx.bonus / 100) + 1;

    let retrofit  = idx.retrofit;
    let health    = 0;
    let firepower = 0;
    let torpedo   = 0;
    let antiair   = 0;
    let aviation  = 0;
    let reload    = 0;
    let hit       = 0;
    let evasion   = 0;
    let luck      = 0;
    let asw       = 0;

    const enhance = (x) =>
    {
      if (x === "firepower")
        return idx.firepower;

      if (x === "torpedo")
        return idx.torpedo;

      if (x === "aviation")
        return idx.aviation;

      if (x === "reload")
        return idx.reload;

      return 0;
    };

    if (retrofit)
    {
      const KAI     = SHIP.retrofit;
      const STATS   = SHIP.retrofit_stats;
      const GROWTH  = SHIP.retrofit_growth;

      const statCal = (x) => KAI[x] + Math.floor((STATS[x] + ((GROWTH[x] * (LEVEL - 1)) / 1000) + enhance(x)) * BONUS);
      health    = statCal("health");
      firepower = statCal("firepower");
      torpedo   = statCal("torpedo");
      antiair   = statCal("antiair");
      aviation  = statCal("aviation");
      reload    = statCal("reload");
      hit       = statCal("hit");
      evasion   = statCal("evasion");
      luck      = statCal("luck");
      asw       = statCal("asw");
    }
    else
    {
      const STATS   = SHIP.stats[idx.limit];
      const GROWTH  = SHIP.growth[idx.limit];
      const statCal = (x) => Math.floor((STATS[x] + ((GROWTH[x] * (LEVEL - 1)) / 1000) + enhance(x)) * BONUS);
      health    = statCal("health");
      firepower = statCal("firepower");
      torpedo   = statCal("torpedo");
      antiair   = statCal("antiair");
      aviation  = statCal("aviation");
      reload    = statCal("reload");
      hit       = statCal("hit");
      evasion   = statCal("evasion");
      luck      = statCal("luck");
      asw       = statCal("asw");
    };

    setStatsData({
      health: health,
      firepower: firepower,
      torpedo: torpedo,
      antiair: antiair,
      aviation: aviation,
      reload: reload,
      hit: hit,
      evasion: evasion,
      luck: luck,
      asw: asw
    });

    return null;
  };
  return (
    <>
      <Navbar />
      <div className="wrapper">
        <PageHeader />
        <div className="main text-center align-middle">
          <div className="content">
            <div className="width-50 m-auto d-flex">
              <Input
                type="select"
                name="ship-select"
                id="ship-select"
                className="input_max"
                defaultValue={"000"}
                onChange={shipChange}
                valid
              >
                <option value="000">{"<-- Ship -->"}</option>
                {stats.map((idx, key) => (<option key={key} value={idx.id}>{idx.name}</option>))}
              </Input>
            </div>
            <Profile ship={ship} data={profileData} />
            <Stats stats={statsData} />
          </div>
        </div>
        <Footer />
      </div>
    </>
  );
};

export default Calculator;
