import { remove } from "diacritics";

function raritySearch(ship, status, search)
{
  const NAME   = ship.names.en.toLowerCase();
  const RARITY = ship.rarity;

  if (status[0] && RARITY === "Normal")
    if (search === "") return true;
    else if (NAME.includes(search)) return true;

  if (status[1] && RARITY === "Rare")
    if (search === "") return true;
    else if (NAME.includes(search)) return true;

  if (status[2] && RARITY === "Elite")
    if (search === "") return true;
    else if (NAME.includes(search)) return true;

  if (status[3] && RARITY === "Super Rare")
    if (search === "") return true;
    else if (NAME.includes(search)) return true;

  if (status[4] && RARITY === "Ultra Rare")
    if (search === "") return true;
    else if (NAME.includes(search)) return true;

  if (status[5] && RARITY === "Priority")
    if (search === "") return true;
    else if (NAME.includes(search)) return true;

  if (status[6] && RARITY === "Decisive")
    if (search === "") return true;
    else if (NAME.includes(search)) return true;

  return false;
};

function removeDiacritics(idx)
{
  return remove(idx)
    .replaceAll("μ", "muse")
    .replaceAll(".", "")
    .replaceAll("'", "");
};

export default function shipSearch(search, azurapi)
{
  let filtered = [];
  const SEARCH = removeDiacritics(search)
    .toLowerCase()
    .replaceAll("decisive", "rarity_7")
    .replaceAll("priority", "rarity_6")
    .replaceAll("ultra rare", "rarity_5")
    .replaceAll("super rare", "rarity_4")
    .replaceAll("elite", "rarity_3")
    .replaceAll("rare", "rarity_2")
    .replaceAll("common", "normal")
    .replaceAll("normal", "rarity_1")
    .trim();

  const SEARCH_ARRAY = SEARCH.split(" ");
  const SEARCH_CLEAN = SEARCH_ARRAY
    .filter(x => x !== "rarity_1")
    .filter(x => x !== "rarity_2")
    .filter(x => x !== "rarity_3")
    .filter(x => x !== "rarity_4")
    .filter(x => x !== "rarity_5")
    .filter(x => x !== "rarity_6")
    .filter(x => x !== "rarity_7")
    .filter(x => x !== "research")
    .filter(x => x !== "retrofit")
    .join(" ");

  let {
    rarity_1,
    rarity_2,
    rarity_3,
    rarity_4,
    rarity_5,
    rarity_6,
    rarity_7,
    retrofit,
    name_search
  } = false;

  if (SEARCH_ARRAY.includes("rarity_1")) rarity_1 = true;
  if (SEARCH_ARRAY.includes("rarity_2")) rarity_2 = true;
  if (SEARCH_ARRAY.includes("rarity_3")) rarity_3 = true;
  if (SEARCH_ARRAY.includes("rarity_4")) rarity_4 = true;
  if (SEARCH_ARRAY.includes("rarity_5")) rarity_5 = true;
  if (SEARCH_ARRAY.includes("rarity_6")) rarity_6 = true;
  if (SEARCH_ARRAY.includes("rarity_7")) rarity_7 = true;
  if (SEARCH_ARRAY.includes("research")) { rarity_6 = true; rarity_7 = true };
  if (SEARCH_ARRAY.includes("retrofit")) retrofit = true;

  const RARITY_STATUS = [rarity_1, rarity_2, rarity_3, rarity_4, rarity_5, rarity_6, rarity_7];

  if (!RARITY_STATUS.includes(true) && !retrofit) name_search = true;

  azurapi.forEach((idx) =>
  {
    const NAME = removeDiacritics(idx.names.en.toLowerCase());
    let push_state = false;

    // blank search
    if (SEARCH === "") push_state = true;

    // ship search
    if (name_search && NAME.includes(SEARCH_CLEAN)) push_state = true;

    // retrofit search
    if (retrofit && idx.retrofit)
    {
      if (SEARCH === "retrofit")
        push_state = true;
      else if (RARITY_STATUS.includes(true))
        push_state = raritySearch(idx, RARITY_STATUS, SEARCH_CLEAN);
      else if (NAME.includes(SEARCH_CLEAN))
        push_state = true;
    };

    // rarity search
    if (!push_state && !retrofit)
      push_state = raritySearch(idx, RARITY_STATUS, SEARCH_CLEAN);

    // hullType Search

    // push state
    if (push_state) filtered.push(idx);
  });

  return filtered;
};
