import React, { useState, useEffect } from "react";
import { Route, Routes, useLocation, useNavigate, useParams } from "react-router-dom";

import ProgressLoad from "../components/ProgressLoad.js";
import ErrorMessage from "../components/ErrorMessage.js";

// Equipment Components
import EquipmentRoute from "./Equipment/EquipmentRoute.js";
import EquipmentList  from "./Equipment/EquipmentList.js";
import EquipmentType  from "./Equipment/EquipmentType.js";

const CACHE = {};

async function fetchLink(index, link)
{
  if (CACHE[index])
    return CACHE[index];

  const DATA = await fetch(link).then(res => res.json());
  CACHE[index] = DATA;

  return DATA;
};

function Equipment()
{
  const { pathname }            = useLocation();
  const [error, setError]       = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [azurapi, setAzurapi]   = useState([]);

  useEffect(() => window.scrollTo(0, 0), [pathname]);

  useEffect(() =>
  {
    fetchLink("azurapi", "https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/equipments.json")
      .then(
        (result) => { setIsLoaded(true); setAzurapi(result) },
        (err) => { setIsLoaded(true); setError(err) }
      );
  }, []);

  const navigate = (equipment) => useNavigate(equipment);

  if (error)
    return <ErrorMessage text={error.message} />;
  else if (!isLoaded)
    return <ProgressLoad text="Loading AzurAPI..." />;

  // in parent function because useParams can't be used in class
  const EquipmentRouteMatch = () =>
  {
    const { id }     = useParams();
    const GEAR_ARRAY = [
      "destroyer_guns",
      "light_cruiser_guns",
      "heavy_cruiser_guns",
      "large_cruiser_guns",
      "battleship_guns",
      "ship_torpedoes",
      "submarine_torpedoes",
      "fighter_planes",
      "dive_bomber_planes",
      "torpedo_bomber_planes",
      "seaplanes",
      "anti-air_guns",
      "anti-submarine_equipment",
      "auxiliary_equipment"
    ];

    let id_azurapi = id.toLowerCase();
    const MATCH_TYPE_LIST = GEAR_ARRAY.find(idx => idx === id_azurapi);
    const EQUIPMENT_DATA = azurapi.find(equip => equip.names.en.toLowerCase().replace(/\s+/g, "_") === id_azurapi);

    if (MATCH_TYPE_LIST)
      return (<EquipmentType azurapi={azurapi} navigate={navigate} type={MATCH_TYPE_LIST} />);

    return <EquipmentRoute equipmentData={EQUIPMENT_DATA} paramsId={id} />;
  };

  return (
    <>
      <Routes>
        <Route path=":id" element={<EquipmentRouteMatch />} />
        <Route path="" element={<EquipmentList />} />
      </Routes>
    </>
  );
};

export default Equipment;
