import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table, Modal, ModalBody } from "reactstrap";

import MeowfficerTalent from "../../../assets/json/MeowfficerTalent.json";

import MeowfficerSupport from "../../../assets/AL/MeowfficerSupport.png";
import MeowfficerCommand from "../../../assets/AL/MeowfficerCommand.png";
import MeowfficerTactics from "../../../assets/AL/MeowfficerTactics.png";

const propTypes = {
  modalOpen: PropTypes.bool,
  modalClose: PropTypes.func,
  openSkillModal: PropTypes.func,
  profile: PropTypes.object,
  openTalentModal: PropTypes.func
};

export default class MeowfficerProfile extends Component
{
  render()
  {
  	const { modalOpen, modalClose, openSkillModal, profile } = this.props;

    // profile
    const {
    	banner: BANNER,
      name: NAME,
      logistics: LOGISTICS,
      directives: DIRECTIVES,
      tactics: TACTICS,
      type: TYPE,
      slot: SLOT,
      rarity: RARITY,
      faction: FACTION,
      timer: BUILD_TIME,
      skill: SKILL
    } = profile;

    let meowfficer = MeowfficerTalent[0];
	  for (const TALENT of MeowfficerTalent)
	    if (TALENT.name === NAME)
	      meowfficer = TALENT;

    return (
		  <Modal isOpen={modalOpen} toggle={modalClose} className="meowfficerModal">
		    <ModalBody>
		      <div className="text-center catModalHeader">
		        <img
		          className="img-fluid"
		          referrerPolicy="no-referrer"
		          alt={NAME}
		          src={BANNER}
		        />
		        <br/>
		        <strong>{NAME}</strong>
		      </div>
			    <Table bordered className="catModalTable catModalTableAlign">
			      <thead>
			        <tr>
			          <th>
			            <img src={MeowfficerSupport} className="img-fluid" referrerPolicy="no-referrer" alt="logistics" />
			            <br />Logistics
			          </th>
			          <th>
			            <img src={MeowfficerCommand} className="img-fluid" referrerPolicy="no-referrer" alt="directives" />
			            <br />Directives
			          </th>
			          <th>
			            <img src={MeowfficerTactics} className="img-fluid" referrerPolicy="no-referrer" alt="tactics" />
			            <br />Tactics
			          </th>
			        </tr>
			      </thead>
		        <tbody>
		          <tr>
		            <td>{LOGISTICS}</td>
		            <td>{DIRECTIVES}</td>
		            <td>{TACTICS}</td>
		          </tr>
		          <tr>
		          	<th colSpan="3">Talent</th>
		          </tr>
		          {meowfficer.talent.map((talent, key) =>
	          	{
		          	const TALENT_KEY 			 = NAME + "Talent" + key;
		          	const TALENT_TYPE 		 = talent.type;
		          	const TALENT_PRIMARY 	 = talent.primary;
		          	const TALENT_SECONDARY = talent.secondary;
		          	const TALENT_OTHER 		 = talent.other;
		          	const openTalentModal = () =>
		          	{
		          		this.props.openTalentModal({
		          			talentType: TALENT_TYPE,
		          			talentPrimary: TALENT_PRIMARY,
		          			talentSecondary: TALENT_SECONDARY,
		          			talentOther: TALENT_OTHER
		          		});
		          	};
		          	return (
		          		<tr key={TALENT_KEY} onClick={openTalentModal}>
				          	<th colSpan="3" className="linkCS">{TALENT_TYPE}</th>
				          </tr>
		          	);
		          })}
		          <tr onClick={openSkillModal}>
		            <th>Skill</th>
		            <th colSpan="2" className="linkCS">{SKILL}</th>
		          </tr>
		          <tr>
		            <th>Skill Type</th>
		            <td colSpan="2">{TYPE}</td>
		          </tr>
		          <tr>
		            <th>Skill Slot</th>
		            <td colSpan="2">{SLOT}</td>
		          </tr>
		          <tr>
		            <th>Rarity</th>
		            <td colSpan="2">{RARITY}</td>
		          </tr>
		          <tr>
		            <th>Faction</th>
		            <td colSpan="2">{FACTION}</td>
		          </tr>
		          <tr>
		            <th>Training Time</th>
		            <td colSpan="2">{BUILD_TIME}</td>
		          </tr>
		        </tbody>
		      </Table>
		    </ModalBody>
		  </Modal>
    );
  };
};

MeowfficerProfile.propTypes = propTypes;
