import React, { Component } from "react";
import PropTypes from "prop-types";
// import { Link } from "react-router-dom";
import { Table } from "reactstrap";

// components
import TableHead from "./Desktop/TableHead.js";
import TableBody from "./Desktop/TableBody.js";

const propTypes = {
  navigate: PropTypes.any,
  type: PropTypes.string,
  equipmentList: PropTypes.array
};

export default class Desktop extends Component
{
  render()
  {
    const { navigate, type, equipmentList } = this.props;

    return (
      <Table className="desktop-view">
        <TableHead type={type} />
        <TableBody type={type} list={equipmentList} navigate={navigate} />
      </Table>
    );
  };
};

Desktop.propTypes = propTypes;
