import React, { Component } from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import UnknownBanner from "../../../assets/AL/UnknownBanner.png";

const propTypes = {
  ship: PropTypes.any,
  banner: PropTypes.any
};

export default class ShipCard extends Component
{
  render()
  {
    const { ship, banner } = this.props;

    const RARITY = ship.rarity;
    const SHIP_ID = ship.id;
    const SHIP_GID = ship._gid;

    let ship_name = ship.names.en;
    if (SHIP_ID === "30001") ship_name = "Hiryuu META";
    if (SHIP_ID === "30002") ship_name = "Ark Royal META";

    const SHIP_LINK   = encodeURIComponent(ship_name.replace(/\s+/g, "_"));

    let banner_rarity = "rarity-0";
    let default_banner = UnknownBanner;

    if (banner) default_banner = banner.skins[0].banner;

    if (RARITY === "Normal")
      banner_rarity = "rarity-1";

    if (RARITY === "Rare")
      banner_rarity = "rarity-2";

    if (RARITY === "Elite")
      banner_rarity = "rarity-3";

    if (RARITY === "Super Rare" || RARITY === "Priority")
      banner_rarity = "rarity-4";

    if (RARITY === "Ultra Rare" || RARITY === "Decisive")
      banner_rarity = "rarity-5";

    return (
      <div className="cardCS shipCard" key={SHIP_GID}>
        <Link to={`/ship/${SHIP_LINK}`} className="shipName linkCS">
          <div className={banner_rarity + " bannerContainer"}>
            <img src={default_banner} referrerPolicy="no-referrer" alt={ship_name} className="img-fluid shipBanner" />
          </div>
          <br />
          {ship_name}
        </Link >
      </div>
    );
  };
};

ShipCard.propTypes = propTypes;
