import React, { Component } from "react";
import PropTypes from "prop-types";
import { Progress } from "reactstrap";

const propTypes = {
  text: PropTypes.string,
  value: PropTypes.number
};

export default class ProgressLoad extends Component
{
  render()
  {
    const { text, value } = this.props;

    let value_time = Math.floor(Math.random() * 100);
    if (value) value_time = value;

    return (
      <div className="progress-container progress-danger azurapi-load width-95 m-auto">
        <span className="progress-badge">{text}</span>
        <Progress max="100" value={value_time} />
      </div>
    );
  };
};

ProgressLoad.propTypes = propTypes;
