import React, { Component } from "react";
import PropTypes from "prop-types";
import { Table } from "reactstrap";

const propTypes = { obtainedFrom: PropTypes.string };

export default class Obtained extends Component
{
  render()
  {
    const { obtainedFrom } = this.props;
    if (obtainedFrom !== "")
    {
      return (
        <Table>
          <thead>
            <tr>
              <th>Obtained From</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>{obtainedFrom}</td>
            </tr>
          </tbody>
        </Table>
      );
    };
    return null;
  };
};

Obtained.propTypes = propTypes;
