import React, { Component } from "react";
import PropTypes from "prop-types";
import wNumb from "wnumb";
import Slider from "nouislider-react";
import { Button, Container, Row, Col } from "reactstrap";

const propTypes = { ship: PropTypes.object, data: PropTypes.func };
export default class Profile extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      ship: {},
      level: 125,
      limit: 3,
      bonus: 6,
      retrofit: false,
      retrofitState: false,
      enhance: [false, false, false, false],
      firepower: 0,
      torpedo: 0,
      aviation: 0,
      reload: 0
    };
  };

  componentDidUpdate(prevProps, prevState)
  {
    const SHIP = this.props.ship;
    if (prevState.ship !== SHIP)
    {
      let retrofit  = false;
      let enhance   = [false, false, false, false];
      let firepower = 0;
      let torpedo   = 0;
      let aviation  = 0;
      let reload    = 0;
      if (SHIP.enhance && SHIP.enhance.health === 0)
      {
        enhance = [
          SHIP.enhance.firepower ? true : false,
          SHIP.enhance.torpedo ? true : false,
          SHIP.enhance.aviation ? true : false,
          SHIP.enhance.reload ? true : false
        ];

        firepower = SHIP.enhance.firepower;
        torpedo   = SHIP.enhance.torpedo;
        aviation  = SHIP.enhance.aviation;
        reload    = SHIP.enhance.reload;
      };

      if (SHIP.retrofit)
      {
        retrofit = true;
        enhance = [
          SHIP.retrofit_enhance.firepower ? true : false,
          SHIP.retrofit_enhance.torpedo ? true : false,
          SHIP.retrofit_enhance.aviation ? true : false,
          SHIP.retrofit_enhance.reload ? true : false
        ];

        firepower = SHIP.retrofit_enhance.firepower;
        torpedo   = SHIP.retrofit_enhance.torpedo;
        aviation  = SHIP.retrofit_enhance.aviation;
        reload    = SHIP.retrofit_enhance.reload;
      };

      this.setState({
        ship: SHIP,
        level: 125,
        limit: 3,
        bonus: 6,
        retrofit: retrofit,
        retrofitState: retrofit,
        enhance: enhance,
        firepower: firepower,
        torpedo: torpedo,
        aviation: aviation,
        reload: reload
      });
    };

    if (prevState !== this.state)
      this.props.data(this.state);
  };

  limiter = (type, value) =>
  {
    if (type === "level") this.setState({ level: parseInt(value) });
    if (type === "limit") this.setState({ limit: parseInt(value) });
    const LIMIT = this.state.limit;
    const LEVEL = this.state.level;
    if (LIMIT === 0 && LEVEL > 70 || LIMIT === 3 && LEVEL < 70) this.setState({ level: 70 });
    if (LIMIT === 1 && LEVEL > 80) this.setState({ level: 80 });
    if (LIMIT === 2 && LEVEL > 90) this.setState({ level: 90 });
    if (LIMIT === 1 && LEVEL < 10) this.setState({ level: 10 });
    if (LIMIT === 2 && LEVEL < 30) this.setState({ level: 30 });
  };

  levelChange = (value) => this.limiter("level", value);
  limitChange = (value) => this.limiter("limit", value);
  bonusChange = (value) => this.setState({ bonus: parseInt(value) });
  enhanceFirepower = (value) => this.setState({ firepower: parseInt(value) });
  enhanceTorpedo = (value) => this.setState({ torpedo: parseInt(value) });
  enhanceAviation = (value) => this.setState({ aviation: parseInt(value) });
  enhanceReload = (value) => this.setState({ reload: parseInt(value) });

  retrofitChange = () => this.setState({ retrofit: !this.state.retrofit });

  render()
  {
    const {
      ship,
      retrofit,
      retrofitState,
      level,
      limit,
      bonus,
      enhance,
      firepower,
      torpedo,
      aviation,
      reload
    } = this.state;

    const {
      retrofitChange,
      levelChange,
      limitChange,
      bonusChange,
      enhanceFirepower,
      enhanceTorpedo,
      enhanceAviation,
      enhanceReload
    } = this;

    const DUMMY_ENHANCE = { firepower: 1, torpedo: 1, aviation: 1, reload: 1 };
    const _ENHANCE_ = ship.enhance ? ship.enhance : DUMMY_ENHANCE;
    const ENHANCE   = ship.retrofit_enhance ? ship.retrofit_enhance : _ENHANCE_;
    const FIREPOWER = ENHANCE.firepower ? ENHANCE.firepower : 1;
    const TORPEDO   = ENHANCE.torpedo ? ENHANCE.torpedo : 1;
    const AVIATION  = ENHANCE.aviation ? ENHANCE.aviation : 1;
    const RELOAD    = ENHANCE.reload ? ENHANCE.reload : 1;

    return (
      <>
        <div className="mt-1">
          <Button
            color="warning"
            outline={!retrofit}
            disabled={!retrofitState}
            onClick={retrofitChange}
            children="Retrofit"
          />
        </div>
        <div className="width-80 m-auto">
          <h4 className="mt-3 mb-2 font-weight-bold">{"<LEVEL>"}</h4>
          <Slider
            id="level-slider"
            className="slider slider-primary tooltip-slider"
            range={{ min: 0, max: 125 }}
            tooltips={[wNumb({ decimals: 0 })]}
            step={1}
            start={level}
            connect="lower"
            onChange={levelChange}
          />
          <h4 className="mt-2 font-weight-bold">{level}</h4>
        </div>
        <Container>
          <Row>
            <Col>
              <h4 className="mt-5 mb-2 font-weight-bold">{"<LIMIT BREAK>"}</h4>
              <Slider
                id="limitbreak-slider"
                className="slider slider-success tooltip-slider"
                range={{ min: 0, max: 3 }}
                tooltips={[wNumb({ decimals: 0 })]}
                behaviour="smooth-steps"
                step={1}
                start={limit}
                connect="lower"
                onChange={limitChange}
              />
              <h4 className="mt-2 font-weight-bold">{limit}</h4>
            </Col>
            <Col>
              <h4 className="mt-5 mb-2 font-weight-bold">{"<AFFINITY>"}</h4>
              <Slider
                id="oath-slider"
                className="slider slider-red tooltip-slider"
                range={{ "min": 0, "8.3%": [1, 2], "33.33%": [3, 3], "max": 12 }}
                tooltips={[wNumb({ decimals: 0, suffix: "%" })]}
                behaviour="smooth-steps"
                step={1}
                start={bonus}
                connect="lower"
                onChange={bonusChange}
              />
              <h4 className="mt-2 font-weight-bold">{bonus}%</h4>
            </Col>
          </Row>
        </Container>
        <Container id="enhance-ship" className={enhance.includes(true) ? "m-auto" : "d-none"}>
          <h4 className="mt-5 mb-2 font-weight-bold">{"<ENHANCE>"}</h4>
          <Row>
            <Col>
              <h6 className="mb-2 font-weight-bold">{"<FIREPOWER>"}</h6>
              <Slider
                id="enhance-firepower-slider"
                className="slider slider-danger tooltip-slider"
                range={{ min: 0, max: FIREPOWER }}
                tooltips={[wNumb({ decimals: 0 })]}
                behaviour="smooth-steps"
                step={1}
                start={firepower}
                connect="lower"
                onChange={enhanceFirepower}
                disabled={!enhance[0]}
              />
              <h6 className="mt-2 font-weight-bold">{firepower}</h6>
            </Col>
            <Col>
              <h6 className="mb-2 font-weight-bold">{"<TORPEDO>"}</h6>
              <Slider
                id="enhance-torpedo-slider"
                className="slider slider-info tooltip-slider"
                range={{ min: 0, max: TORPEDO }}
                tooltips={[wNumb({ decimals: 0 })]}
                behaviour="smooth-steps"
                step={1}
                start={torpedo}
                connect="lower"
                onChange={enhanceTorpedo}
                disabled={!enhance[1]}
              />
              <h6 className="mt-2 font-weight-bold">{torpedo}</h6>
            </Col>
          </Row>
          <Row>
            <Col>
              <h6 className="mt-3 mb-2 font-weight-bold">{"<AVIATION>"}</h6>
              <Slider
                id="enhance-aviation-slider"
                className="slider slider-primary tooltip-slider"
                range={{ min: 0, max: AVIATION }}
                tooltips={[wNumb({ decimals: 0 })]}
                behaviour="smooth-steps"
                step={1}
                start={aviation}
                connect="lower"
                onChange={enhanceAviation}
                disabled={!enhance[2]}
              />
              <h6 className="mt-2 font-weight-bold">{aviation}</h6>
            </Col>
            <Col>
              <h6 className="mt-3 mb-2 font-weight-bold">{"<RELOAD>"}</h6>
              <Slider
                id="enhance-reload-slider"
                className="slider slider-warning tooltip-slider"
                range={{ min: 0, max: RELOAD }}
                tooltips={[wNumb({ decimals: 0 })]}
                behaviour="smooth-steps"
                step={1}
                start={reload}
                connect="lower"
                onChange={enhanceReload}
                disabled={!enhance[3]}
              />
              <h6 className="mt-2 font-weight-bold">{reload}</h6>
            </Col>
          </Row>
        </Container>
      </>
    );
  }
};

Profile.propTypes = propTypes;
