import React, { Component } from "react";
import PropTypes from "prop-types";
import { Modal, ModalBody } from "reactstrap";
import Tag from "../../../components/Tag.js";

const propTypes = {
  modal: PropTypes.bool,
  close: PropTypes.func,
  data: PropTypes.array
};

const TAG_LIST = [
  ["Normal", "#ccc", "#000"],
  ["Rare", "#9fe8ff", "#000"],
  ["Elite", "#c4adff", "#000"],
  ["Super Rare", "#ee9", "#000"],
  ["Ultra Rare", "linear-gradient(120deg,#fbffca,#baffbf,#a7efff,#ffabff)", "#000"],
  ["Priority", "#ee9", "#000"],
  ["Decisive", "linear-gradient(120deg,#fbffca,#baffbf,#a7efff,#ffabff)", "#000"],
  ["Research", "#e99", "#000"],
  ["Retrofit", "#e99", "#000"],
  ["Vanguard", "#e99", "#000"],
  ["Main", "#e99", "#000"],
  ["Submarine", "#e99", "#000"]
  // ["DD", "#9a9", "#000"],
  // ["CL", "#9a9", "#000"],
  // ["CA", "#9a9", "#000"],
  // ["CB", "#9a9", "#000"],
  // ["BB", "#9a9", "#000"],
  // ["BBV", "#9a9", "#000"],
  // ["BM", "#9a9", "#000"],
  // ["CV", "#9a9", "#000"],
  // ["CVL", "#9a9", "#000"],
  // ["AR", "#9a9", "#000"],
  // ["AE", "#9a9", "#000"],
  // ["SS", "#9a9", "#000"],
  // ["SSV", "#9a9", "#000"],
  // ["Eagle Union", "#9e9", "#000"],
  // ["Royal Navy", "#9e9", "#000"],
  // ["Sakura Empire", "#9e9", "#000"],
  // ["Iron Blood", "#9e9", "#000"],
  // ["Dragon Empery", "#9e9", "#000"],
  // ["Sardegna Empire", "#9e9", "#000"],
  // ["Northern Parliament", "#9e9", "#000"],
  // ["Iris Libre", "#9e9", "#000"],
  // ["Vichya Dominion", "#9e9", "#000"],
  // ["META", "#9e9", "#000"],
  // ["Universal", "#9e9", "#000"],
  // ["Collab", "#9e9", "#000"],
  // ["MGM1", "#999", "#000"],
  // ["MGM2", "#999", "#000"],
  // ["MGM3", "#999", "#000"],
  // ["DD AUX", "#999", "#000"],
  // ["DD AOA", "#999", "#000"],
  // ["DD TRP", "#999", "#000"],
  // ["Fighter", "#999", "#000"],
  // ["Dive Bomber", "#999", "#000"],
  // ["Torpedo Bomber", "#999", "#000"],
  // ["Anti Air", "#999", "#000"],
  // ["Torpedo", "#999", "#000"],
  // ["Heal", "#999", "#000"],
  // ["Preload", "#999", "#000"],
  // ["Buff", "#999", "#000"],
  // ["Debuff", "#999", "#000"]
];

export default class ShipModal extends Component
{
  constructor(props)
  {
    super(props);
    this.state = { tagList: [] };
  };

  componentDidMount()
  {
    const { data } = this.props;
    const { tagList } = this.state;

    if (data !== tagList)
      this.setState({ tagList: data });
  };

  tagClick = (event) =>
  {
    const TARGET = event.target.innerHTML;
    let tagList  = this.state.tagList;
    if (this.state.tagList.includes(TARGET))
      tagList = tagList.filter(x => x !== TARGET);
    else
      tagList.push(TARGET);

    this.setState({ tagList: tagList });
  };

  tagClear = () => this.setState({ tagList: [] });

  closeModal = () =>
  {
    const { close } = this.props;
    close(this.state.tagList);
  };

  // conclusion tag search is only available for rarity + research + retrofit
  render()
  {
    const { modal } = this.props;
    const { tagList } = this.state;
    const { tagClick, tagClear, closeModal } = this;

  	return (
      <Modal isOpen={modal} toggle={closeModal} size="xl" className="pb-5 dark">
        <div className="modal-header dis-inlineblock text-center align-middle">
          <h1 className="dis-inlineblock">
            <i className="tim-icons icon-tag tim-icons-32"/>
            <br/>Tag
          </h1>
          <button
            type="button"
            className="close modal-close"
            data-dismiss="modal"
            aria-hidden="true"
            onClick={closeModal}
          >
            <i className="tim-icons icon-simple-remove font30px" />
          </button>
        </div>
        <ModalBody>
          <Tag text="Clear" color="#F00" font="#FFF" onClick={tagClear} />
          <div className="tagContainer">
            {tagList.length ? tagList.map((idx, key) =>
            {
              const TAG_NAME = idx;
              const TAG_DATA = TAG_LIST.find(x => x[0] === TAG_NAME);

              if (TAG_DATA)
                return (
                  <Tag
                    key={"tag" + key}
                    text={TAG_DATA[0]}
                    color={TAG_DATA[1]}
                    font={TAG_DATA[2]}
                    onClick={tagClick}
                  />
                );

              return null;
            }) : <Tag text="None" color="#fff" font="#000" />}
          </div>
          <div className="tagBullet">TAG LIST</div>
          <div>
            {TAG_LIST.map((idx, key) =>
            {
              const TAG   = idx[0];
              const COLOR = idx[1];
              const FONT  = idx[2];
              return <Tag key={key} text={TAG} color={COLOR} font={FONT} onClick={tagClick} />;
            })}
          </div>
          <div className="p-10"/>
        </ModalBody>
      </Modal>
    );
  };
};

ShipModal.propTypes = propTypes;
