import React, { Component } from "react";
import PropTypes from "prop-types";
import classnames from "classnames";

const propTypes = {
  src: PropTypes.string,
  id: PropTypes.string,
  alt: PropTypes.string,
  className: PropTypes.string
};

export default class ImgR extends Component
{
  render()
  {
    const { src, id, alt, className } = this.props;

    return (
      <img
        id={id}
        src={src}
        referrerPolicy="no-referrer"
        alt={alt ? alt : "..."}
        className={classnames("img-fluid ImgR-Icon", className)}
      />
    );
  };
};

ImgR.propTypes = propTypes;
