import React, { Component } from "react";
import PropTypes from "prop-types";

// custom components
import ImgR from "../../../../../components/ImgR.js";

const propTypes = {
  slot: PropTypes.string,
  onClick: PropTypes.func,
  background: PropTypes.string,
  mount: PropTypes.string,
  efficiency: PropTypes.string,
  reload: PropTypes.string,
  icon: PropTypes.string,
  name: PropTypes.string,
  desc: PropTypes.string,
  auxiliary: PropTypes.bool
};

export default class Slot extends Component
{
  render()
  {
    const {
      slot,
      onClick,
      background,
      mount,
      efficiency,
      reload,
      icon,
      name,
      desc,
      auxiliary
    } = this.props;

    let extend_icon = <></>;

    if (!auxiliary)
      extend_icon = (
        <>
          <div id={"equipment-max-" + slot}>+{mount}</div>
          <div id={"equipment-efficiency-" + slot}>{efficiency}%</div>
          <div id={"equipment-reload-" + slot}>{reload}</div>
        </>
      );

    return (
      <tr>
        <td onClick={onClick} className="equipment-body-icon">
          <div className={"equipment-" + background + " m-auto"}>
            {extend_icon}
            <ImgR id={"equipment-icon-" + slot} src={icon} />
          </div>
        </td>
        <td>
          <div id={"equipment-type-" + slot}>{name}</div>
          <div id={"equipment-desc-" + slot}>{desc}</div>
        </td>
      </tr>
    );
  };
};

Slot.propTypes = propTypes;
