import React, { Component } from "react";
import PropTypes from "prop-types";
import {
  Chart as ChartJS,
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
} from "chart.js";
import { Radar } from "react-chartjs-2";

ChartJS.register(
  RadialLinearScale,
  PointElement,
  LineElement,
  Filler,
  Tooltip,
  Legend
);

const propTypes = {
  name: PropTypes.string,
  data: PropTypes.array
};

export default class ShipChart extends Component
{
  render()
  {
    const SHIP_NAME    = this.props.name;
    const HEXAGON_DATA = this.props.data;

    const HEXAGON = [
      HEXAGON_DATA[0],
      HEXAGON_DATA[5],
      HEXAGON_DATA[4],
      HEXAGON_DATA[3],
      HEXAGON_DATA[2],
      HEXAGON_DATA[1]
    ];
    const DATA = {
      labels: ["FP", "HP", "AA", "EVA", "AVI", "TRP"],
      datasets: [
        {
          label: SHIP_NAME,
          data: HEXAGON,
          backgroundColor: "rgba(200, 200, 200, 0.3)",
          borderColor: "rgba(255, 255, 255, 1)",
          borderWidth: 1
        }
      ]
    };

    const OPTIONS = {
      plugins: { legend: { display: false } },
      scales: {
        r: {
          suggestedMin: -1,
          suggestedMax: 5,
          ticks: { stepSize: 1, display: false },
          pointLabels: {
            color: "rgba(255, 255, 255, 0.7)",
            font: {
              family: "'Poppins', sans-serif",
              size: 12,
              weight: 700,
              lineHeight: 1.5
            }
          },
          angleLines: { color: "red" },
          grid: { color: ["blue", "teal", "green", "yellow", "orange", "red"] }
        }
      }
    };

    return <div className="shipChart m-auto"><Radar data={DATA} options={OPTIONS} /></div>;
  };
};

ShipChart.propTypes = propTypes;
