const RESEARCH = ["Priority", "Decisive"];
const VANGUARD = [
  "Destroyer",
  "Light Cruiser",
  "Heavy Cruiser",
  "Large Cruiser",
  "Cargo Ship"
];
const MAIN = [
  "Battleship",
  "Aviation Battleship",
  "Monitor",
  "Aircraft Carrier",
  "Light Aircraft Carrier",
  "Repair Ship"
];
const SUBMARINE = [
  "Submarine",
  "Submarine Carrier"
];

export default function shipFilter(azurapi, tags)
{
  if (!tags.length) return azurapi;

  const CONTAINER = [];

  azurapi.forEach(ship =>
  {
    const RARITY = ship.rarity;
    const HULLTYPE = ship.hullType;
    const RETROFIT = ship.retrofit ? true : false;

    let push = false;
    if (tags.includes(RARITY))
      push = true;

    if (tags.includes("Research") && RESEARCH.includes(RARITY))
      push = true;

    if (tags.includes("Retrofit") && RETROFIT)
      push = true;

    if (tags.includes("Vanguard") && VANGUARD.includes(HULLTYPE))
      push = true;

    if (tags.includes("Main") && MAIN.includes(HULLTYPE))
      push = true;

    if (tags.includes("Submarine") && SUBMARINE.includes(HULLTYPE))
      push = true;

    if (push) CONTAINER.push(ship);
  });

  return CONTAINER;
};
