import React, { Component } from "react";
import PropTypes from "prop-types";

const propTypes = {
  src: PropTypes.string,
  className: PropTypes.string,
  id: PropTypes.string,
  none: PropTypes.bool
};

export default class Fits extends Component
{
  render()
  {
    const { src, className, id, none } = this.props;

    if (none)
      return <th className={className} id={id} />;

    return (
      <th className={className} id={id} >
        <img src={src} referrerPolicy="no-referrer" alt={id} className="img-fluid" />
      </th>
    );
  };
};

Fits.propTypes = propTypes;

