import React, { Component } from "react";
import PropTypes from "prop-types";

// image
import Empty from "../../../../assets/AL/Empty.png";

// custom components
import Slot from "./Equipment/Slot.js";

// function
import highestRarity from "../../../../components/highestRarity.js";

const propTypes = {
  dataBuild: PropTypes.object,
  gear: PropTypes.any,
  gearData: PropTypes.object,
  gearSlot: PropTypes.object,
  openGearModal: PropTypes.func
};

export default class Equipment extends Component
{
  render()
  {
    const DATA_BUILD = this.props.dataBuild;
    const GEAR       = highestRarity(this.props.gear);
    const GEAR_DATA  = this.props.gearData;

    const openGearModal = (idx) =>
    {
      const SHIP_ID   = document.getElementById("ship-select").value.toString();
      const SHIP_TYPE = document.getElementById("slot-ship-type").textContent.toString();
      const SHIP_KAI  = document.getElementById("retrofit-select").value;
      let kai_boolean = false;

      if (SHIP_KAI === "0") kai_boolean = false;
      if (SHIP_KAI === "1") kai_boolean = true;

      this.props.openGearModal({ slot: idx, id: SHIP_ID, type: SHIP_TYPE, kai: kai_boolean });
    };

    const openGearModal1 = () => openGearModal(1);
    const openGearModal2 = () => openGearModal(2);
    const openGearModal3 = () => openGearModal(3);
    const openGearModal4 = () => openGearModal(4);
    const openGearModal5 = () => openGearModal(5);

    const SHIP_ID  = DATA_BUILD.id;
    const SHIP_API = DATA_BUILD.azurapi;

    const EQUIPMENT_1 = GEAR_DATA.primary;
    const EQUIPMENT_2 = GEAR_DATA.secondary;
    const EQUIPMENT_3 = GEAR_DATA.tertiary;
    const EQUIPMENT_4 = GEAR_DATA.auxiliary_1;
    const EQUIPMENT_5 = GEAR_DATA.auxiliary_2;

    // default equipment icon
    let equipment_icon_1 = Empty;
    let equipment_icon_2 = Empty;
    let equipment_icon_3 = Empty;
    let equipment_icon_4 = Empty;
    let equipment_icon_5 = Empty;

    // default equipment background
    let equipment_bg_1 = "empty-slot";
    let equipment_bg_2 = "empty-slot";
    let equipment_bg_3 = "empty-slot";
    let equipment_bg_4 = "empty-slot";
    let equipment_bg_5 = "empty-slot";

    // default equipment slot
    let equipment_type_1 = "Primary";
    let equipment_type_2 = "Secondary";
    let equipment_type_3 = "Tertiary";
    let equipment_type_4 = "Auxliary";
    let equipment_type_5 = "Auxliary";

    // default gun/plane mount
    let equipment_max_1 = 1;
    let equipment_max_2 = 1;
    let equipment_max_3 = 1;

    // default gun/plane efficiency
    let equipment_efficiency_1 = 100;
    let equipment_efficiency_2 = 100;
    let equipment_efficiency_3 = 100;

    // default equipment description
    let equipment_description_1 = "";
    let equipment_description_2 = "";
    let equipment_description_3 = "";
    let equipment_description_4 = "";
    let equipment_description_5 = "";

    if (SHIP_ID !== "000")
    {
      equipment_type_1 = SHIP_API.slots[0].type;
      equipment_type_2 = SHIP_API.slots[1].type;
      equipment_type_3 = SHIP_API.slots[2].type;

      equipment_max_1 = SHIP_API.slots[0].max;
      equipment_max_2 = SHIP_API.slots[1].max;
      equipment_max_3 = SHIP_API.slots[2].max;

      equipment_efficiency_1 = SHIP_API.slots[0].maxEfficiency;
      equipment_efficiency_2 = SHIP_API.slots[1].maxEfficiency;
      equipment_efficiency_3 = SHIP_API.slots[2].maxEfficiency;
    };

    if (EQUIPMENT_1 !== "000")
    {
      const EQUIPMENT_DATA = GEAR.find(idx => idx.id === EQUIPMENT_1);
      equipment_icon_1 = EQUIPMENT_DATA.image;
      equipment_type_1 = EQUIPMENT_DATA.names.en;
      equipment_bg_1   = "rarity-" + EQUIPMENT_DATA.highest_rarity;
      equipment_description_1 = EQUIPMENT_DATA.misc.notes;
    };

    if (EQUIPMENT_2 !== "000")
    {
      const EQUIPMENT_DATA = GEAR.find(idx => idx.id === EQUIPMENT_2);
      equipment_icon_2 = EQUIPMENT_DATA.image;
      equipment_type_2 = EQUIPMENT_DATA.names.en;
      equipment_bg_2   = "rarity-" + EQUIPMENT_DATA.highest_rarity;
      equipment_description_2 = EQUIPMENT_DATA.misc.notes;
    };

    if (EQUIPMENT_3 !== "000")
    {
      const EQUIPMENT_DATA = GEAR.find(idx => idx.id === EQUIPMENT_3);
      equipment_icon_3 = EQUIPMENT_DATA.image;
      equipment_type_3 = EQUIPMENT_DATA.names.en;
      equipment_bg_3   = "rarity-" + EQUIPMENT_DATA.highest_rarity;
      equipment_description_3 = EQUIPMENT_DATA.misc.notes;
    };

    if (EQUIPMENT_4 !== "000")
    {
      const EQUIPMENT_DATA = GEAR.find(idx => idx.id === EQUIPMENT_4);
      equipment_icon_4 = EQUIPMENT_DATA.image;
      equipment_type_4 = EQUIPMENT_DATA.names.en;
      equipment_bg_4   = "rarity-" + EQUIPMENT_DATA.highest_rarity;
      equipment_description_4 = EQUIPMENT_DATA.misc.notes;
    };

    if (EQUIPMENT_5 !== "000")
    {
      const EQUIPMENT_DATA = GEAR.find(idx => idx.id === EQUIPMENT_5);
      equipment_icon_5 = EQUIPMENT_DATA.image;
      equipment_type_5 = EQUIPMENT_DATA.names.en;
      equipment_bg_5   = "rarity-" + EQUIPMENT_DATA.highest_rarity;
      equipment_description_5 = EQUIPMENT_DATA.misc.notes;
    };

    return (
      <>
        <thead className="mobile-equipment-head">
          <tr>
            <th className="width-15">SLOT</th>
            <th className="width-85">EQUIPMENT</th>
          </tr>
        </thead>
        <tbody className="mobile-equipment-body">
          <Slot
            slot="1"
            onClick={openGearModal1}
            background={equipment_bg_1}
            mount={equipment_max_1}
            efficiency={equipment_efficiency_1}
            reload={"00.00s"}
            icon={equipment_icon_1}
            name={equipment_type_1}
            desc={equipment_description_1}
          />
          <Slot
            slot="2"
            onClick={openGearModal2}
            background={equipment_bg_2}
            mount={equipment_max_2}
            efficiency={equipment_efficiency_2}
            reload={"00.00s"}
            icon={equipment_icon_2}
            name={equipment_type_2}
            desc={equipment_description_2}
          />
          <Slot
            slot="3"
            onClick={openGearModal3}
            background={equipment_bg_3}
            mount={equipment_max_3}
            efficiency={equipment_efficiency_3}
            reload={"00.00s"}
            icon={equipment_icon_3}
            name={equipment_type_3}
            desc={equipment_description_3}
          />
          <Slot
            slot="4"
            onClick={openGearModal4}
            background={equipment_bg_4}
            icon={equipment_icon_4}
            name={equipment_type_4}
            desc={equipment_description_4}
            auxiliary
          />
          <Slot
            slot="5"
            onClick={openGearModal5}
            background={equipment_bg_5}
            icon={equipment_icon_5}
            name={equipment_type_5}
            desc={equipment_description_5}
            auxiliary
          />
        </tbody>
      </>
    );
  };
};

Equipment.propTypes = propTypes;
