import React, { Component } from "react";
import PropTypes from "prop-types";
import { Input } from "reactstrap";

const propTypes = {
  defaultValue: PropTypes.bool,
  onChange: PropTypes.func
};

export default class Oath extends Component
{
  render()
  {
    const { defaultValue, onChange } = this.props;

    let default_value = 0;
    if (defaultValue) default_value = 1;

    return (
      <Input
        type="select"
        name="oath-select"
        id="oath-select"
        className="input_max"
        defaultValue={default_value}
        onChange={onChange}
      >
        <option id="oath-select-false" value="0">FALSE</option>
        <option id="oath-select-true" value="1">TRUE</option>
      </Input>
    );
  };
};

Oath.propTypes = propTypes;
