import React, { Component } from "react";
import PropTypes from "prop-types";
import { Input } from "reactstrap";

const propTypes = {
  inputReload: PropTypes.func,
  inputTech: PropTypes.func,
  inputCat: PropTypes.func,
  reload: PropTypes.number,
  tech: PropTypes.number,
  cat: PropTypes.number,
  finalReload: PropTypes.number
};

export default class Reload extends Component
{
  render()
  {
    const { inputReload, inputTech, inputCat, reload, tech, cat, finalReload } = this.props;

    return (
      <thead>
        <tr>
          <th>BASE RELOAD</th>
          <th>FLEET TECH RELOAD</th>
          <th>MEOWFFICER RELOAD</th>
          <th>FINAL RELOAD</th>
        </tr>
        <tr>
          <th>
            <Input
              id="reload-base"
              type="number" min="0"
              className="fleet-input"
              onChange={inputReload}
              defaultValue={reload}
            />
          </th>
          <th>
            <Input
              id="reload-tech"
              type="number" min="0"
              className="fleet-input"
              onChange={inputTech}
              defaultValue={tech}
            />
          </th>
          <th>
            <Input
              id="reload-cat"
              type="number" min="0"
              className="fleet-input"
              onChange={inputCat}
              defaultValue={cat}
            />
          </th>
          <th>
            <Input
              id="reload-final"
              type="number" min="0"
              className="fleet-input-disabled"
              defaultValue={finalReload}
              disabled
            />
          </th>
        </tr>
      </thead>
    );
  };
};

Reload.propTypes = propTypes;
