import React, { Component } from "react";
import PropTypes from "prop-types";
import { Col, NavLink, Card, CardText } from "reactstrap";

const propTypes = {
  tag: PropTypes.object,
  to: PropTypes.string
};

export default class EquipmentCard extends Component
{
  render()
  {
    const { tag, to } = this.props;
    const CARD_LINK = "/equipment/" + to.toLowerCase().replace(/ /g, "_");
    return (
      <Col xs="12" sm="6">
        <div>
          <NavLink tag={tag} to={CARD_LINK}>
            <Card body outline color="primary">
              <CardText>{to}</CardText>
            </Card>
          </NavLink>
        </div>
      </Col>
    );
  };
};

EquipmentCard.propTypes = propTypes;

